@import"https://fonts.googleapis.com/css2?family=DM+Sans:ital,opsz,wght@0,9..40,100..1000;1,9..40,100..1000&display=swap";::before,::after,*{font-family:"DM Sans",sans-serif;padding:0;margin:0;box-sizing:border-box}html,body{width:100%;height:100%}ul,ol{list-style:none}a{text-decoration:none;color:#6b6b6b;padding:10px}::selection{background:#ffb7b7}body.dark-theme{background-color:#1a1a1a;color:#fff}body.dark-theme h1,body.dark-theme h2,body.dark-theme h3,body.dark-theme h4,body.dark-theme h5,body.dark-theme h6{color:#fff}body.dark-theme a{color:#4a9eff}body.dark-theme a:hover{color:#66b3ff}body.dark-theme .card,body.dark-theme .container,body.dark-theme .section{background-color:#2d2d2d;box-shadow:0 4px 6px rgba(0,0,0,.3)}body.dark-theme button,body.dark-theme .button{background-color:#4a9eff;color:#fff}body.dark-theme button:hover,body.dark-theme .button:hover{background-color:#66b3ff}body.dark-theme input,body.dark-theme textarea,body.dark-theme select{background-color:#333;color:#fff;border:1px solid #4a4a4a}body.dark-theme input:focus,body.dark-theme textarea:focus,body.dark-theme select:focus{border-color:#4a9eff}body.dark-theme table{background-color:#2d2d2d;color:#fff}body.dark-theme table th,body.dark-theme table td{border-color:#4a4a4a}body.dark-theme table tr:hover{background-color:#333}nav{background-color:#333;display:flex;flex-direction:row;align-items:center;justify-content:space-between;gap:10px;flex-wrap:no-wrap;padding:20px 150px}nav .right{display:flex;flex-direction:row;align-items:center;justify-content:space-between;gap:20px;flex-wrap:no-wrap}nav .right .right-right{display:flex;flex-direction:row;align-items:center;justify-content:space-between;gap:20px;flex-wrap:no-wrap}